$(document).ready(function() {
    new WOW().init();
    $('header').scrollToFixed({ zIndex: 110 });
    $(window).bind('scroll', function() {
        if ($(window).scrollTop() > 100) {
            $('header').addClass('minimal');
        } else {
            $('header').removeClass('minimal');
        }
    });
    // if ($('.homepage').length > 0) {
    //     $('#fullpage').fullpage({
    //         scrollOverflow: true,
    //         scrollingSpeed: 500,
    //         navigation: true,
    //         navigationPosition: 'right',
    //         anchors: ['firstPage', '2Page', '3Page', '4Page', '5Page', '6Page', '7Page', '8Page', 'lastPage'],
    //         afterLoad: function afterLoad(anchorLink, index) {
    //             var loadedSection = $(this);
    //             //using anchorLink
    //             if (anchorLink == 'firstPage') {
    //                 $('header').removeClass('minimal');
    //             } else {
    //                 $('header').addClass('minimal');
    //             }
    //         }
    //     });
    // }
    // $(window).bind('scroll', function() {
    //     if ($(window).scrollTop() > 100) {
    //         $('header').addClass('minimal');
    //     } else {
    //         $('header').removeClass('minimal');
    //     }
    // });

    $('.totop').click(function() {
        $('body').animate({
            scrollTop: 0
        }, 500);
    });

    $('.btn-showmenu').click(function() {
        $('.menu').toggleClass('open');
        $('html').css('overflow', 'auto');
        $(this).toggleClass('active');
        $('.overlay').fadeIn(500);
    });

    $('.btn-closemenu').click(function() {
        $('.menu').removeClass('open');
        $('html').css('overflow', 'auto');
        $('.btn-showmenu').removeClass('active');
        $('.overlay').fadeOut(500);
    });

    $('.overlay').click(function() {
        $(this).fadeOut(500);
        $('.menu').removeClass('open');
        $('.search').removeClass('open');
        $('html').css('overflow', 'auto');
        $('.btn-showmenu').removeClass('active');
    });
    $('.searchtoggle').click(function() {
        $('.search').toggleClass('open');
        $('.overlay').fadeIn(500);
        $('.menu').removeClass('open');
        // $('html').css('overflow', 'hidden');
    });
    $('.btn-showmega').click(function() {
        $('.mega').fadeIn(300);
    });
    $('.btn-closemega').click(function() {
        $('.mega').fadeOut(300);
    });
    $('.btn-showsub').click(function() {
        $('.sub').fadeToggle(1000);
    });
    $('.btn-show-tabs').click(function() {
        $('.nav-tabs').slideToggle(300);
    });
    //BANNER
    $('.banner').slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        fade: true,
        speed: 1000,
        autoplay: true,
        autoplaySpeed: 5000,
        dots: false,
        arrows: true
    });
    $('.list-sale').slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        speed: 1000,
        dots: true,
        arrows: false
    });
    $('.list-expert').slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        speed: 1000,
        dots: true,
        arrows: false
    });
    $('.list-solution').slick({
        slidesToShow: 4,
        slidesToScroll: 1,
        arrows: true,
        responsive: [{
                breakpoint: 991,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 1,
                }
            },
            {
                breakpoint: 767,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                }
            }
        ]
    });
    $('.list-news').slick({
        slidesToShow: 3,
        slidesToScroll: 1,
        arrows: true,
        responsive: [{
                breakpoint: 991,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 1,
                }
            },
            {
                breakpoint: 767,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 3,
                }
            }
        ]
    });
    //PRODUCT
    $('.list-product').slick({
        slidesToShow: 4,
        slidesToScroll: 1,
        lazyLoad: 'ondemand',
        arrows: true,
        dots: false,
        speed: 500,
        // autoplay: true,
        autoplaySpeed: 5000,
        responsive: [{
            breakpoint: 1199,
            settings: {
                slidesToShow: 3,
                slidesToScroll: 3,
                infinite: true,
                dots: true
            }
        }, {
            breakpoint: 767,
            settings: {
                slidesToShow: 2,
                slidesToScroll: 2
            }
        }, {
            breakpoint: 480,
            settings: {
                slidesToShow: 2,
                slidesToScroll: 1
            }
        }]
    });

    $('.prd-home a[data-toggle="tab"]').on('shown.bs.tab', function(e) {

        e.preventDefault();
        var id = $(this).attr('href');
        $(id + ' .prd-slide').slick('unslick');

        $(id + ' .prd-slide').slick({
            slidesToShow: 4,
            slidesToScroll: 1,
            lazyLoad: 'ondemand',
            arrows: true,
            dots: false,
            speed: 500,
            autoplay: true,
            autoplaySpeed: 5000,
            responsive: [{
                breakpoint: 1199,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                    infinite: true,
                    dots: true
                }
            }, {
                breakpoint: 767,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2
                }
            }, {
                breakpoint: 480,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1
                }
            }, {
                breakpoint: 360,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                }
            }]
        });
        //  $(id + " .slick-slider").hide();      
        //  $(id + ' .slick-next').trigger('click');
        //  $(id + " .slick-slider").show();    
        //$(id + ' .prd-related , .prdzone-slide').resize();product-zone-list
    });

    $('.product-slide').slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false,
        dots: false,
        infinite: false,
        fade: true,
        asNavFor: '.product-nav'
    });
    $('.product-nav').slick({
        slidesToShow: 4,
        slidesToScroll: 1,
        asNavFor: '.product-slide',
        dots: false,
        focusOnSelect: true,
        swipe: true,
        swipeToSlide: true,
        vertical: true,
        verticalSwiping: true,
        responsive: [{
            breakpoint: 1199,
            settings: {
                slidesToShow: 4
            }
        }, {
            breakpoint: 991,
            settings: {
                slidesToShow: 4
            }
        }, {
            breakpoint: 767,
            settings: {
                slidesToShow: 3
            }
        }, {
            breakpoint: 543,
            settings: {
                slidesToShow: 3,
                vertical: false,
                verticalSwiping: false
            }
        }]
    });
    $('.lastview-slide').slick({
        slidesToShow: 4,
        slidesToScroll: 1,
        dots: false,
        arrows: true,
        autoplay: false,
        speed: 500,
        responsive: [{
            breakpoint: 991,
            settings: {
                slidesToShow: 3,
            }
        }, {
            breakpoint: 767,
            settings: {
                slidesToShow: 2,
            }
        }]
    });
    //IMAGE HOVER
    $(document).on('mouseenter', '.category-step [data-toggle="tab"]', function() {
        $(this).tab('show');
    });
    // FILTER
    $('.btn-showcate').click(function() {
        $('.filter-category').addClass('open');
    });
    $('.btn-closefilter').click(function() {
        $('.filter-wrap').removeClass('open');
    });

    //CART
    $('#btn-xuathd').click(function() {
        $('.bill-form').slideToggle(300);
    });
    $('.cart-lastview-slide').slick({
        slidesToShow: 4,
        slidesToScroll: 1,
        arrows: true,
        responsive: [{
                breakpoint: 1199,
                settings: {
                    slidesToShow: 4,
                    slidesToScroll: 1,
                }
            },
            {
                breakpoint: 991,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 1,
                }
            },
            {
                breakpoint: 767,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                }
            },
            {
                breakpoint: 543,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                }
            }
        ]
    });
    $('.map-ifarme').fancybox({
        type: "iframe",
        padding: 0,
        maxWidth: 800,
        maxHeight: 460,
        // 'autoDimensions':   true,
        // fitToView: false,
        iframe: {
            preload: false
        },
        helpers: {
            overlay: {
                locked: false
            }
        }
    });
    // DISABLE SCROLL MAP
    $('#map').click(function() {
            $(this).find('iframe').addClass('active')
        })
        .mouseleave(function() {
            $(this).find('iframe').removeClass('active')
        });
});

